.theme-category{
    border: 1px solid #212529;
    background: red;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    cursor: pointer;
    margin: 5px;
    box-shadow: 0px 0px 5px aqua;
}
.theme-category:hover{
    transform: scale(1.1);
    transition: .3s ease-in-out all;
}
.theme-selection{
    display: flex;
    flex-direction: row;
}
#purple{
    background: purple;
}
#blue{
    background: blue;
}
#black{
    background: black;
}
#white{
    background: white;
}